import Vue from 'vue'
import VueRouter from 'vue-router'
import { wLayout } from '@/components'
import Login from '@/views/login'
import Index from '@/views/index'
import noPage from '@/views/404'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name:  'login',
    component: Login,
    meta: { title: '登录' }
  },
  {
    path: '*',
    name:  '404',
    component: noPage,
    meta: { title: '没有此页面' }
  },
  {
    path: '/',
    name:  'index',
    component: Index,
    children: [
      {
        path: '/goods',
        redirect: '/goods/list',
        name:  '套餐',
        component: wLayout,
        children: [
          {
            path: '/goods/list',
            name: 'goodsList',
            component: () => import(/* webpackChunkname: 'goods' */ '@/views/goods/list.vue'),
            meta: { title: '套餐列表' }
          },
          {
            path: 'edit/:id',
            name: 'goodsEdit',
            component: () => import(/* webpackChunkname: 'goods' */ '@/views/goods/edit.vue'),
            meta: { title: '编辑套餐' }
          }
        ]
      },
      {
        path: '/order',
        redirect: '/order/list',
        name:  '预约单',
        component: wLayout,
        children: [
          {
            path: 'list',
            name: 'orderList',
            component: () => import(/* webpackChunkname: 'order' */ '@/views/order/list.vue'),
            meta: { title: '预约单列表' }
          },
          {
            path: 'edit/:id',
            name: 'orderEdit',
            component: () => import(/* webpackChunkname: 'order' */ '@/views/order/edit.vue'),
            meta: { title: '编辑预约单' }
          }
        ]
      },
      {
        path: '/activity',
        redirect: '/activity/list',
        name:  '活动',
        component: wLayout,
        children: [
          {
            path: 'list',
            name: 'activityList',
            component: () => import(/* webpackChunkname: 'activity' */ '@/views/activity/list.vue'),
            meta: { title: '活动列表' }
          },
          {
            path: 'edit/:id',
            name: 'activityEdit',
            component: () => import(/* webpackChunkname: 'activity' */ '@/views/activity/list.vue'),
            meta: { title: '编辑活动' }
          }
        ]
      },
      {
        path: '/user',
        redirect: '/user/list',
        name:  '用户',
        component: wLayout,
        children: [
          {
            path: 'list',
            name: 'userList',
            component: () => import(/* webpackChunkname: 'user' */ '@/views/user/list.vue'),
            meta: { title: '用户列表' }
          }
        ]
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
